<template>
    <section>
        <loader :is-visible="isLoading" />

        <div class="container-fluid invoice-payment p-0" v-if="templatedata.length">

            <!-- Company Selection -->

            <div class="col-sm-12 col-md-12 float-left col-lg-12 col-xl-12 bg-lightgrey py-4">
                <div class="row">

                    <div id="title" class="col-sm-12 col-md-12 float-left col-lg-6 col-xl-6">
                        <div class="form-group">
                            <label>{{ $t('YouareWorkingWith') }}:</label>
                            <input type="text" class="form-control" :value="templatedata[0].companyname" />

                        </div>
                    </div>

                    <div id="title" class="col-sm-12 col-md-12 float-left col-lg-6 col-xl-6">
                        <div class="form-group">
                            <label>{{ $t('ChooseAnotherCompany') }}:</label>
                            <select v-model="selectedCompanyId" class="form-control">
                                <option disabled value="">{{ $t('SelectCompany') }}</option>
                                <option v-for="company in companies" :key="company.value" :value="company.value">
                                    {{ company.name }}
                                </option>
                            </select>
                        </div>
                    </div>

                </div>
            </div>

            <!-- Bill Details -->

            <div class="theme-primary partition">
                <span class="p-name text-white">{{ $t('BillPayment') }}</span>
                <span class="p-actions float-right">
                    <a title="Back to List" href="javascript:;" @click="goToInvoiceListing"
                        class="p-action-btn text-white ">
                        <em class="fa fa-angle-double-left"></em>
                        <br /><span> {{ $t('BacktoList') }}</span></a>
                </span>

            </div>
            <div class="col-sm-12 col-md-12 float-left col-lg-12 col-xl-12 border mb-4 py-3">

                <div class="card probx">
                    <div class="card-header">
                        <h2 class="mb-0">{{ $t('BillDetail') }}</h2>
                    </div>
                    <div class="card-body">
                        <div class="form-row">
                            <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4">
                                <div class="form-group"><label><strong>{{ $t('BillNumber') }}:</strong> {{
                                    templatedata[0].invoicenumber }}</label></div>
                            </div>
                            <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4">
                                <div class="form-group"><label><strong>{{ $t('DueDate') }}:</strong> {{
                                    templatedata[0].duedate }}</label></div>
                            </div>
                            <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4">
                                <div class="form-group"><label><strong>{{ $t('TotalAmount') }} ({{
                                    templatedata[0].isocode || '-' }}):</strong>
                                        {{ templatedata[0].totalamountyouowe != null ? templatedata[0].totalamountyouowe
                                            :
                                            '-' }}</label></div>
                            </div>
                            <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4">
                                <div class="form-group"><label><strong>{{ $t('Paid Amount') }}:</strong>
                                         {{ $n(PaymentAmount, "currency", $getLocaleByCurrency(CurrencyCode))}}</label></div>
                            </div>
                            <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4">
                                <div class="form-group"><label><strong>{{ $t('RemainingAmountDue') }}:</strong>
                                         {{ $n(TotalDueAmount, "currency", $getLocaleByCurrency(CurrencyCode))}}</label></div>
                            </div>
                        </div>
                    </div>
                </div>

                <div class="card probx mt-4">
                    <div class="card-header">
                        <h2 class="mb-0">{{ $t('From') }}</h2>
                    </div>
                    <div class="card-body">
                        <div class="form-row">
                            <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4">
                                <div class="form-group"><label><strong>{{ $t('CompanyName') }}:</strong> {{
                                    templatedata[0].companyname }}</label></div>
                            </div>
                            <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4">
                                <div class="form-group"><label><strong>{{ $t('Address') }}:</strong> {{
                                    templatedata[0].companyaddress }}</label></div>
                            </div>
                            <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4">
                                <div class="form-group"><label><strong>{{ $t('EmailId') }}:</strong> {{
                                    templatedata[0].email_id }}</label></div>
                            </div>
                            <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4">
                                <div class="form-group"><label><strong>{{ $t('ProjectName') }}:</strong> {{
                                    templatedata[0].project_name }}</label></div>
                            </div>
                            <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4">
                                <div class="form-group"><label><strong>{{ $t('Phone') }}:</strong> {{
                                    templatedata[0].contact_mobile_phone }}</label></div>
                            </div>
                        </div>
                    </div>
                </div>

                <!-- Payment Section -->
                <div class="card probx mt-4">
                    <div class="card-header">
                        <h2 class="mb-0">{{ $t('Payment') }}</h2>
                    </div>
                    <div class="card-body">
                        <!-- Payment Method -->
                        <div id="ProcessPayment" class="mb-3">
                            <div class="form-group">
                                <label class="font-weight-bold">{{ $t('PaymentMethod') }}:</label>
                            </div>
                            <div v-if="noGateways" class="alert alert-danger text-center">
                                {{ $t('NoPaymentGatewayDefined') || 'No Payment gateway defined!' }}
                            </div>
                            <div v-else class="clientwalletsprite">
                                <div v-for="(item, index) in paymentGateways" :key="index"
                                    class="col-sm-12 col-lg-12 col-md-12 mb-0 p-0">
                                    <button type="button" class="btn w-100 py-3"
                                        :class="[item.PaymantGatewayType, '', { active: selectedGateway === item.PaymentGatewayCode }]"
                                        @click="selectGateway(item.PaymentGatewayCode)">
                                         <em :class="selectedGateway === item.PaymentGatewayCode ? 'fa fa-check-circle float-left font-20 text-success' : 'fa fa-circle-thin float-left font-20'"></em>
                                        <!-- {{ item.PaymantGatewayType }} -->
                                    </button>
                                    <!-- <button type="button" class="btn w-100 py-3"
                                    :class="['btn-outline-secondary', { 'btn-primary text-white': selectedGateway === item.PaymentGatewayCode }]"
                                    @click="selectGateway(item.PaymentGatewayCode)">
                                    {{ item.PaymantGatewayType }}
                                </button> -->
                                </div>
                                <div v-if="PaymentLink" class="col-md-4 mb-3">
                                    <a :href="PaymentLink" target="_blank" class="btn btn-outline-info w-100 py-3">
                                        {{ $t('ExternalLink') || 'External Link' }}
                                    </a>
                                </div>
                            </div>
                            <input type="hidden" name="PaymentGatewayCode" :value="selectedGateway" />
                        </div>

                        <div class="right-forms">
                        <!-- Card Form -->
                        <div v-if="selectedGateway !== 'Wallet' && selectedGateway !== 'PayU' && selectedGateway !== 'Razorpay' && selectedGateway !== 'Stripe'"
                            class="row mb-3 py-3">
                            <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4">
                                <div class="form-group">
                                    <label>{{ $t('CardType') }}:<span class="text-danger">*</span></label>
                                    <select v-model="card.cardType" class="form-control"
                                        :class="{ 'is-invalid': cardErrors.cardType }"
                                        @change="cardErrors.cardType = ''">
                                        <option value="Visa">Visa</option>
                                        <option value="Master">Master Card</option>
                                        <option value="Discover">Discover</option>
                                        <option value="Amex">American Express</option>
                                    </select>
                                    <small class="text-danger" v-if="cardErrors.cardType">{{ cardErrors.cardType
                                    }}</small>
                                </div>
                            </div>

                            <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4">
                                <div class="form-group">
                                    <label>{{ $t('CardNumber') }}:<span class="text-danger">*</span></label>
                                    <input type="text" v-model="card.number" class="form-control"
                                        :class="{ 'is-invalid': cardErrors.number }" @input="cardErrors.number = ''" />
                                    <small class="text-danger" v-if="cardErrors.number">{{ cardErrors.number }}</small>
                                </div>
                            </div>

                            <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4">
                                <div class="form-group">
                                    <label>{{ $t('NameOnCard') }}:<span class="text-danger">*</span></label>
                                    <input type="text" v-model="card.name" class="form-control"
                                        :class="{ 'is-invalid': cardErrors.name }" @input="cardErrors.name = ''" />
                                    <small class="text-danger" v-if="cardErrors.name">{{ cardErrors.name }}</small>
                                </div>
                            </div>

                            <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4">
                                <div class="form-group">
                                    <label>{{ $t('ExpirationDate') }}:<span class="text-danger">*</span></label>
                                    <div class="row no-gutters">
                                        <div class="col-6 pr-1">
                                            <select v-model="card.expMonth" class="form-control"
                                                :class="{ 'is-invalid': cardErrors.expMonth }"
                                                @change="cardErrors.expMonth = ''">
                                                <option disabled value="">Month</option>
                                                <option v-for="(month, index) in months" :key="index"
                                                    :value="(index + 1).toString().padStart(2, '0')">
                                                    {{ month }}
                                                </option>
                                            </select>
                                            <small class="text-danger" v-if="cardErrors.expMonth">{{ cardErrors.expMonth
                                            }}</small>
                                        </div>
                                        <div class="col-6 pl-1">
                                            <select v-model="card.expYear" class="form-control"
                                                :class="{ 'is-invalid': cardErrors.expYear }"
                                                @change="cardErrors.expYear = ''">
                                                <option disabled value="">Year</option>
                                                <option v-for="y in years" :key="y">{{ y }}</option>
                                            </select>
                                            <small class="text-danger" v-if="cardErrors.expYear">{{ cardErrors.expYear
                                            }}</small>
                                        </div>
                                    </div>
                                </div>
                            </div>

                        </div>

                        <div v-show="selectedGateway == 'Stripe' ">
                            <form ref="paymentForm" @submit.prevent="submitPayment">

                                <div class="row py-3">

                                    <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4">
                                        <div class="form-group">
                                            <label for="cardholder-name">Name On
                                                Card:</label>
                                            <input v-model="billingDetails.name" type="text" placeholder="Name On Card"
                                                class="stripe-input w-100  form-control" />
                                        </div>
                                    </div>

                                    <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4">
                                        <div class="form-group">
                                            <label>Card Number: <span class="mandatory">*</span></label>
                                            <div id="card-number-element" class="stripe-input form-control"></div>
                                        </div>
                                    </div>

                                    <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4">
                                        <div class="form-group">
                                            <label>Expiry Date: <span class="mandatory">*</span></label>
                                            <div id="card-expiry-element" class="stripe-input form-control"></div>
                                        </div>
                                    </div>

                                    <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4">
                                        <div class="form-group">
                                            <label>CVC: <span class="mandatory">*</span></label>
                                            <div id="card-cvc-element" class="stripe-input form-control"></div>
                                        </div>
                                    </div>

                                    <div id="card-errors" class="text-danger">
                                        {{ errorMessage }}</div>

                                </div>


                                <div class="col-12 mt-3 p-0">
                                    <div class="row flex-row-reverse">
                                        <div class="col-lg-6 text-right">
                                            <!-- <button type="submit" class="btn btn-success">Submit</button>  -->
                                            <!-- Hidden fields to be submitted -->
                                            <input type="hidden" ref="stripeToken">
                                            <input type="hidden" ref="hdnCardType">
                                            <input type="hidden" ref="hdnNameonCard">
                                            <input type="hidden" ref="hdnCardNumber">
                                            <input type="hidden" ref="hdnCardMonth">
                                            <input type="hidden" ref="hdnCardYear">
                                            <input type="hidden" ref="hdnCvv">

                                        </div>
                                        
                                    </div>
                                </div>

                            </form>
                        </div>

                        <!-- Amount and Payment Type -->
                        <div v-if="['Stripe','Wallet', 'Authorize', 'PayU', 'Razorpay'].includes(selectedGateway)"
                            class="row mb-3 py-3">
                            <div v-if="selectedGateway === 'Wallet'" class="col-sm-12 col-md-6 col-lg-4 col-xl-4">
                                <div class="form-group">
                                    <label>{{ $t('Available Balance') }} ({{ templatedata[0].isocode }}):</label>
                                    <input type="text" class="form-control" :value="walletBalance" disabled />
                                </div>
                            </div>
                            <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4">
                                <div class="form-group">
                                    <label>{{ $t('RemainingAmountDue') }} ({{ templatedata[0].isocode }}):</label>
                                    <input type="text" class="form-control" :value="TotalDueAmount" disabled />
                                </div>
                            </div>

                            <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4">
                                <div class="form-group">
                                    <label class="d-block mb-2">{{ $t('PaymentType') }}:</label>
                                    <div class="d-flex align-items-center">
                                        <div class="form-check mr-3">
                                            <input type="radio" id="full" value="f" v-model="paymentType"
                                                class="form-check-input" />
                                            <label class="form-check-label" for="full">
                                                {{ $t('FullBalance') }}
                                            </label>
                                        </div>
                                        <div class="form-check">
                                            <input type="radio" id="partial" value="p" v-model="paymentType"
                                                class="form-check-input" />
                                            <label class="form-check-label" for="partial">
                                                {{ $t('PartialPaid') }}
                                            </label>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4" v-if="paymentType === 'p'">
                                <div class="form-group">
                                    <label>Enter Amount ({{ templatedata[0].isocode }}):</label>
                                    <input type="text" class="form-control" :class="{ 'is-invalid': errorMessage }"
                                        v-model="paymentAmount" @input="validateAmount" />
                                    <small v-if="errorMessage" class="text-danger">{{ errorMessage }}</small>

                                </div>
                            </div>
                        </div>


                        <div class="col-12 p-0">
                            <div class="row flex-row-reverse">
                              <div class="col-lg-6 text-right d-flex justify-content-end">
                                <a href="javascript:;" class="btn btn-info" @click="payNow" style="margin-right: 10px;">
                                    <em class="fa fa-credit-card-alt pr-2"></em>{{ $t('PayNow') }}
                                </a>

                                <a href="javascript:;" class="btn btn-danger" @click="cancelPayment">
                                    <em class="fa fa-times pr-2"></em>Cancel
                                </a>
                            </div>


                                <div class="col-lg-6 text-danger"><small>{{ $t('MandatoryString') }}</small></div>
                            </div>

                        </div>
                        </div>

                    </div>
                </div>

            </div>
        </div>

        <!-- <div class="text-center p-4" v-else>
        <p class="text-danger mb-0">Payment gateway not working.</p>
    </div> -->
    </section>
</template>

<script>
import DataService from "../../services/DataService";
export default {
    data() {
        return {
            walletBalance: 0,
            paymentAmount: "",
            errorMessage: "",
            IsUtilityBilling: false,
            paymentHistoryData: [],
            paymentList: [],
            MainInvocieNetAmount: 0,
            totalPayments: 0,
            templatedata: [],
            companies: [],
            selectedCompanyId: "",
            paymentGateways: [],
            selectedGateway: "",
            PaymentLink: "",
            paymentType: "f",
            finalAmount: 0,
            card: {
                cardType: "Visa",
                number: "",
                name: "",
                expMonth: "",
                expYear: "",
            },
            years: [],
            months: [
                "January", "February", "March", "April", "May", "June",
                "July", "August", "September", "October", "November", "December"
            ],
            UserId: "",
            BillIdEnc: "",
            BillId: '',
            cardErrors: {
                cardType: "",
                number: "",
                name: "",
                expMonth: "",
                expYear: "",
            },
            isLoading: false,
            PaymentAmount: 0,
            TotalDueAmount: 0,
            TransactionId: null,
            UniqueId: null,
            ClientEmail: null,
            createdDone: null,

            stripe: null,
            elements: null,
            cardNumber: null,
            cardExpiry: null,
            cardCvc: null,            
            billingDetails: {
                name: ""
            },
            stripePublishableKey:
                "pk_test_51RjfwB4C6gUvp1ei45F8RMBEUh9vCrFMZgb6Qgrs78Rs9ED9gX2H0HpVm0Pv7NlHiGOc7wdJ1OtH7phwoxpJMgMY00tzECaTbs",
        };
    },
    computed: {
        noGateways() {
            return (!this.paymentGateways || this.paymentGateways.length === 0) && !this.PaymentLink;
        },
    },

    watch: {
        async selectedGateway(newVal) {
            
            
            if (newVal !== 'Stripe') return;

            await this.loadStripeScript();
            this.initializeStripe();
        }
    },
    async mounted() {
        ;

        await this.createdDone;

        if (this.selectedGateway !== 'Stripe') return;

        // Load Stripe script before initializing
        await this.loadStripeScript();

        this.initializeStripe();
    },
    created: async function () {
        this.createdDone = (async () => {
            
            // Show loader immediately when component is created
            this.isLoading = true;
            try {
                this.CurrencyCode = this.GetUserInfo.Currency.replace(/[()]/g, '');
                this.UserId = this.GetUserInfo.ID;
                this.ClientEmail = this.GetUserInfo.Email;
                this.BillIdEnc = this.$route.params.billId;
                this.BillId = this.$route.params.InvoiceId;

                await this.CheckExtraFeatures();
                this.generateYears();
                await this.GetActiveGatewayDetail();
                await this.GetConfigurationValueBykey();
                await this.GetInvoiceDetail();
                await this.GetClientBillingDetailsAndAddress();
                await this.GetCompanyListing();
                await this.AddPaymentTranaction();
            } catch (err) {
                // Log error but ensure loader is hidden
                console.error('Error during created lifecycle:', err);
            } finally {
                // Always hide loader after startup sequence completes (success or failure)
                this.isLoading = false;
            }
        })();
    },
    watch: {
        selectedGateway() {
            this.errorMessage = "";
            this.paymentAmount = "";
        }
    },
    methods: {
        cancelPayment() {
            
            this.$router.push('/ClientBill/InvoiceListing');
        },
        validateAmount() {
            
            var vm = this;
            if (!this.paymentAmount || isNaN(this.paymentAmount) || parseFloat(this.paymentAmount) <= 0) {
                this.errorMessage = "Please enter a valid positive amount.";
                return;
            }
            if (this.selectedGateway) {
                if (parseFloat(this.paymentAmount) > vm.TotalDueAmount) {
                    this.errorMessage = `Entered amount exceeds the remaining due amount of ${vm.TotalDueAmount}`;
                    return;
                } else {
                    this.errorMessage = "";
                }
            } else {
                this.errorMessage = "";
            }
            if (this.selectedGateway === 'Wallet') {

                if (parseFloat(this.paymentAmount) > this.walletBalance) {
                    this.errorMessage = `Entered amount exceeds the available wallet balance of ${this.walletBalance}`;
                } else {
                    this.errorMessage = "";
                }
            } else {
                this.errorMessage = "";
            }
        },
        async CheckExtraFeatures(type) {

            var vm = this;
            const params = `featureCode=GET_EXTRA_FEATURE_BILLING&outputType=list`;
            try {
                const response = await DataService.CheckExtraFeatures(params);
                if (response.data) {
                    const features = JSON.parse(response.data);
                    features.forEach(feature => {
                        if (feature.FEATURE_CODE === 'UTILITY_BILLING') {
                            vm.IsUtilityBilling = true;
                        } else {
                            vm.IsUtilityBilling = false;
                        }
                    });
                }
            } catch (error) {
                console.log("Error checking extra features:", error);
            }
        },
        generateYears() {

            const currentYear = new Date().getFullYear();
            for (let i = 0; i < 15; i++) {
                this.years.push(currentYear + i);
            }
        },
        async GetInvoiceDetail() {
            debugger
            var vm = this;
            var type = vm.IsUtilityBilling == true ? 'UTILITY' : 'Subscription';

            //const params = `billId=${this.BillIdEnc}&sourceType=${type}`;
            const params = `billId=${this.BillIdEnc}&sourceType=${this.IsUtilityBilling}`;
            const response = await DataService.getInvoiceDetails(params);
            this.templatedata = response.data.result.data;
            vm.finalAmount = vm.templatedata[0]?.nettotal1 || 0;
            vm.PaymentAmount = 0;

            var lastPayment = response.data?.result?.data?.[0]?.lastpaymentdetails || [];
            lastPayment.forEach(item => {
                const cleanAmount = String(item.paymentamount).replace(/,/g, '');
                vm.PaymentAmount += Number(cleanAmount) || 0;     
            });             
           
            vm.TotalDueAmount = (Number(vm.finalAmount) || 0) - vm.PaymentAmount;
        },
        async GetConfigurationValueBykey() {

            const params = `configkey=PaymentLink`;
            try {
                const response = await DataService.GetBaseCurrency(params);
                this.PaymentLink = response.data.CONFIG_DATA_VALUE;
            } catch (err) {
                console.error(err);
            }
        },
        async CheckExtraFeatures(type) {
            var vm = this;
            const params = `featureCode=GET_EXTRA_FEATURE_BILLING&outputType=list`;
            try {
                const response = await DataService.CheckExtraFeatures(params);
                if (response.data) {
                    const features = JSON.parse(response.data);
                    features.forEach(feature => {
                        if (feature.FEATURE_CODE === 'UTILITY_BILLING') {
                            vm.IsUtilityBilling = 'UTILITY';
                        } else {
                            vm.IsUtilityBilling = 'SUBSCRIPTION';
                        }
                    });
                }
            } catch (error) {

                console.log("Error checking extra features:", error);
            }
        },
        goToInvoiceListing() {
            this.$router.push('/ClientBill/InvoiceListing');
        },
        async GetClientBillingDetailsAndAddress() {
            
            const params = `clientId=${this.UserId}&projectId=`;

            try {
                const response = await DataService.GetClientBillingDetailsAndAddress(params);
                if (response.data.Status === "Success") {
                    // Handle billing data if needed
                }
            } catch (err) {
                console.error(err);
            }
        },
        async GetCompanyListing() {

            try {
                const response = await DataService.GetCompanyList();
                this.companies = [];
                let companyList = response.data.result;
                if (typeof companyList === 'string') {
                    companyList = JSON.parse(companyList);
                }

                if (Array.isArray(companyList) && companyList.length > 0) {
                    companyList.forEach(item => {
                        this.companies.push({
                            name: item.COMPANY_NAME,
                            value: item.COMPANY_ID
                        });
                    });
                }
            } catch (err) {
                this.companies = [];
                console.error(err);
            }
        },
        async GetActiveGatewayDetail() {
            debugger
            try {
                var vm = this;
                const response = await DataService.GetActiveGatewayDetail();
                if (response.data?.result) {
                    const parsedGateways = JSON.parse(response.data.result);
                    parsedGateways.forEach(item => {
                        if (item.parameters) {
                            try {
                                item.parameters = JSON.parse(item.parameters);
                            } catch (e) {
                                console.warn("Invalid parameters JSON:", item.parameters);
                                item.parameters = {};
                            }
                        }
                    });
                    vm.paymentGateways = parsedGateways;

                    //  Automatically select the first gateway if available
                    if (vm.paymentGateways.length > 0) {
                        vm.selectedGateway = vm.paymentGateways[0].PaymentGatewayCode;
                    }

                } else {
                    vm.paymentGateways = [];
                }
            } catch (err) {
                console.error("Error loading gateways:", err);
                vm.paymentGateways = [];
            }
        },
        selectGateway(code) {
            debugger
            this.selectedGateway = code;
            if (code === 'Wallet') {
                const selectedWallet = this.paymentGateways.find(item => item.PaymentGatewayCode === 'Wallet');
                if (selectedWallet) {
                    this.walletBalance = selectedWallet.WalletBalance;  // Assign wallet balance
                }
            }
        },
        loadStripeScript() {
            
            return new Promise((resolve, reject) => {
                // If already loaded, resolve immediately
                if (document.querySelector('script[src="https://js.stripe.com/v3/"]')) {
                    resolve();
                    return;
                }

                const script = document.createElement("script");
                script.src = "https://js.stripe.com/v3/";
                script.onload = () => resolve();
                script.onerror = () => reject("Stripe failed to load");
                document.head.appendChild(script);
            });
        },
        initializeStripe() {
            
            // Initialize Stripe and Elements
            this.stripe = Stripe(this.stripePublishableKey);
            this.elements = this.stripe.elements();

            const style = {
                base: {
                    fontSize: "14px",
                    color: "#212529",
                    "::placeholder": { color: "#888" }
                },
                invalid: { color: "#dc3545" }
            };

            // Create elements
            this.cardNumber = this.elements.create("cardNumber", { style });
            this.cardExpiry = this.elements.create("cardExpiry", { style });
            this.cardCvc = this.elements.create("cardCvc", { style });

            // Mount elements
            this.cardNumber.mount("#card-number-element");
            this.cardExpiry.mount("#card-expiry-element");
            this.cardCvc.mount("#card-cvc-element");

            // Handle errors
            [this.cardNumber, this.cardExpiry, this.cardCvc].forEach(el => {
                el.on("change", evt => {
                    this.errorMessage = evt.error ? evt.error.message : "";
                });
            });
        },
        mapStripeError(error) {
            
            const codes = {
                incomplete_number: "Please enter a complete card number.",
                invalid_number: "The card number you entered is invalid.",
                incomplete_cvc: "Please enter your card's CVC.",
                invalid_cvc: "The CVC code you entered is incorrect.",
                incomplete_expiry: "Please enter the full expiration date.",
                invalid_expiry_year_past: "Your card's expiration year is in the past.",
                card_declined: "Your card was declined. Please try another payment method.",
                expired_card: "Your card has expired."
            };

            return codes[error.code] || error.message || "Something went wrong.";
        },

        async payNow() {
            debugger
            var vm = this;
                 if (vm.TotalDueAmount < 0) {
        vm.ShowAlert("Remaining amount cannot be negative.", "failure", true, "Alert");
        return;
    }

   if (vm.TotalDueAmount === 0) {
    vm.ShowAlert("Remaining amount should be positive.", "failure", true, "Alert");
    return;
}
            vm.isLoading = true;

            if (vm.paymentType === 'p') {
                vm.validateAmount();
                if (vm.errorMessage) {
                    vm.isLoading = false;
                    return;
                }
            }

            const finalAmount = vm.templatedata[0]?.nettotal1 || 0;

            if (vm.selectedGateway === 'Wallet') {
                var vm = this;
                var walletObj = {
                    TransactionId: vm.TransactionId?.toString() || "0",
                    UniqueId: vm.UniqueId || "",
                    CompanyName: vm.templatedata[0]?.companyname || '',
                    CustomerId: vm.templatedata[0]?.Customer_id || '',
                    InvoiceId: vm.BillId,
                    NetAmount: vm.TotalDueAmount,
                    PaidAmount: vm.paymentType === 'f' ? vm.TotalDueAmount : vm.paymentAmount,
                    PaymentGatewayCode: vm.selectedGateway,
                    Description: 'BILL_PAYMENT',
                    PaymentType: vm.paymentType,
                    firstname: vm.templatedata[0]?.clientname || ''
                };

                vm.confirmR(vm.$t("Do You Want To Proceed With Payment ?"), true, vm.$t("Proceed With Payment"), function () {
                    vm.isLoading = true;

                    var objectPostString = JSON.stringify(walletObj);
                    DataService.MakeRealPartialpayment(objectPostString)
                        .then(function (response) {
                            vm.isLoading = false;
debugger
          
                   if (response.data && response.data.result) {
                    var result = response.data.result;
                    var  Jsonresult=   JSON.parse(result)
                   if (Jsonresult && Jsonresult.CODE === '3') {
                        vm.isLoading = false;
                        vm.walletBalance = Jsonresult.wallet_current_balance; 
                        vm.ShowAlert(vm.$t(Jsonresult.MSG || 'Balance Not Available'), "failure", true, vm.$t('Alert'));
                    }
                    else if (response.data.redirectUrl) {
                        const redirectUrl = response.data.redirectUrl;
                        window.location.href = redirectUrl;
                    } 
                    else {
                        vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                    }
                } else {
                    vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                }
                        })
                        .catch(function (error) {
                            vm.isLoading = false;
                            vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                        });
                });

                vm.isLoading = false;
            }

            else if (vm.selectedGateway === 'PayU') {
                

                var payuObj = {

                    txnid: vm.TransactionId?.toString() || "0",
                    udf1: vm.UniqueId || "",
                    amount: vm.paymentType === 'p' ? vm.paymentAmount?.toString() : vm.TotalDueAmount?.toString() || "0",
                    productinfo: "BILL_PAYMENT",
                    firstname: vm.templatedata[0]?.clientname || '',
                    email: vm.templatedata[0]?.email_id || '',
                    phonenumber: vm.templatedata[0]?.phone || '',
                    udf2: vm.templatedata[0]?.billId || '',
                    udf3: vm.selectedGateway

                };

                DataService.MakeRealPartialpaymentPAYU(payuObj).then(function (response) {
                    vm.isLoading = false;

                    if (response.data && typeof response.data === "string") {

                        window.location.href = response.data;
                        return;
                    }

                    vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                });

                return;
            }

            else if (vm.selectedGateway === 'Razorpay') {

                var razorpayObj = {
                    amount: vm.paymentType === 'p' ? vm.paymentAmount?.toString() : vm.TotalDueAmount?.toString() || "0",
                    currency: "USD",
                    name: vm.templatedata[0]?.clientname || '',
                    email: vm.templatedata[0]?.email_id || '',
                    transactionId: vm.TransactionId?.toString() || "0",
                    notes: "BILL_PAYMENT",
                    invoice_id: vm.BillId,
                    user_id: vm.GetUserInfo.ID,
                    company_id: vm.GetUserInfo.CompanyId,
                    reference_no: vm.UniqueId?.toString() || "0",
                    BillIdEnc: vm.BillIdEnc
                };

                vm.$router.push({
                    name: "RazorpayCheckout",
                    params: {
                        razorpayData: razorpayObj
                    }
                });
                // DataService.CreatepaymentRAZORPAY(razorpayObj)
                //     .then(function (response) {
                //         vm.isLoading = false;

                //         console.log("Razorpay Order Response: ", response.data);

                //         if (response.data?.razorpayOrderId) {
                //             vm.ShowAlert("Order Created Successfully", "success", true, "Alert");
                //         } else {
                //             vm.ShowAlert("Order Creation Failed", "failure", true, "Alert");
                //         }
                //     })
                //     .catch(function (error) {
                //         console.error("Razorpay API Error:", error);
                //         vm.ShowAlert("Server Error", "failure", true, "Alert");
                //     });

                // return;
            }

            else if (vm.selectedGateway === 'Stripe') {

                const { token, error } = await vm.stripe.createToken(vm.cardNumber, {
                    name: vm.billingDetails.name
                });

                if (error) {
                    //if (window.hideLoader) hideLoader();
                    vm.errorMessage = vm.mapStripeError(error);
                    vm.isLoading = false;
                    return;
                }

                // Fill hidden fields
                vm.$refs.stripeToken.value = token.id;
                vm.$refs.hdnCardType.value = token.card.brand;
                vm.$refs.hdnNameonCard.value = token.card.name;
                vm.$refs.hdnCardNumber.value = token.card.last4;
                vm.$refs.hdnCardMonth.value = token.card.exp_month;
                vm.$refs.hdnCardYear.value = token.card.exp_year;
                vm.$refs.hdnCvv.value = 123;

                var Obj = {
                    CardMonth: vm.$refs.hdnCardMonth.value,
                    CardNum: vm.$refs.hdnCardNumber.value,
                    CardType: vm.$refs.hdnCardType.value,
                    CardYear: vm.$refs.hdnCardYear.value,
                    CompanyName: vm.templatedata[0]?.companyname || '',
                    CustomerId: '',
                    InvoiceId: vm.BillId,
                    NameOnCard: vm.$refs.hdnNameonCard.value,
                    NetAmount: vm.TotalDueAmount,
                    PaidAmount: vm.paymentType === 'f' ? vm.TotalDueAmount : vm.paymentAmount,
                    PaymentGatewayCode: 'Stripe',
                    RefId: vm.templatedata[0]?.ref_id || '',
                    BillIdEnc: vm.templatedata[0]?.ref_id || '',
                    Description: 'BILL_PAYMENT',
                    StripeToken: vm.$refs.stripeToken.value,
                    UniqueId: vm.UniqueId,
                    TransactionId: vm.TransactionId,
                    ClientEmail: vm.ClientEmail

                }
                var JsonObj = JSON.stringify(Obj);

                // Submit form
                //this.$refs.paymentForm.submit();

                DataService.MakeRealPartialpayment(JsonObj).then(function (response) {
                    vm.isLoading = false;

                    if (response.data.status == 1) {

                        vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Payment")]), "success", true, vm.$t("Alert"));
                        vm.$router.push({ name: 'InvoiceDetail' });

                    } else if (response.data.status == 2) {

                        vm.ShowAlert(vm.$t("InvalidLoginCredentials"), "failure", true, vm.$t('Alert'));

                    } else {
                        vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                    }

                });
                vm.isLoading = false;
                return;
            }

            else {

                let valid = true;

                if (!this.card.cardType) {
                    this.cardErrors.cardType = "Card type is required.";
                    valid = false;
                }
                if (!this.card.number) {
                    this.cardErrors.number = "Card number is required.";
                    valid = false;
                } else if (!/^\d{13,19}$/.test(this.card.number)) {
                    this.cardErrors.number = "Invalid card number.";
                    valid = false;
                }
                if (!this.card.name) {
                    this.cardErrors.name = "Name on card is required.";
                    valid = false;
                }
                if (!this.card.expMonth) {
                    this.cardErrors.expMonth = "Expiration month is required.";
                    valid = false;
                }
                if (!this.card.expYear) {
                    this.cardErrors.expYear = "Expiration year is required.";
                    valid = false;
                }

                if (!valid) return;

                var Obj = {
                    CardMonth: vm.card.expMonth,
                    CardNum: vm.card.number,
                    CardType: vm.card.cardType,
                    CardYear: vm.card.expYear,
                    CompanyName: vm.templatedata[0]?.companyname || '',
                    CustomerId: vm.templatedata[0]?.Customer_id || '',
                    InvoiceId: vm.BillId,
                    NameOnCard: vm.card.name,

                    // FIXED
                    NetAmount: this.TotalDueAmount,

                    PaidAmount: vm.paymentType === 'f'
                        ? vm.TotalDueAmount
                        : vm.paymentAmount,

                    PaymentGatewayCode: vm.selectedGateway,
                    RefId: vm.templatedata[0]?.ref_id || '',
                    BillIdEnc: vm.templatedata[0]?.ref_id || '',
                    Description: 'BILL_PAYMENT',
                    StripeToken: null,
                    UniqueId: vm.UniqueId,
                    TransactionId: vm.TransactionId,
                    ClientEmail: vm.ClientEmail
                };

                DataService.MakeRealPartialpayment(JSON.stringify(Obj)).then(function (response) {
                    vm.isLoading = false;

                    if (response.data.status == 1) {
                        vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Payment")]), "success", true, vm.$t("Alert"));
                        vm.$router.push({ name: 'InvoiceDetail' });

                    } else if (response.data.status == 2) {
                        vm.ShowAlert(vm.$t("InvalidLoginCredentials"), "failure", true, vm.$t('Alert'));

                    } else {
                        vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                    }
                });
            }
        },
        async AddPaymentTranaction() {
            
            var vm = this;
            const finalAmount = vm.templatedata[0]?.nettotal1 || 0;
            const amount = vm.paymentType === 'p' ? (vm.paymentAmount?.toString() || "0") : (finalAmount?.toString() || "0");
            const url = `Amount=${amount}&Description=BILL_PAYMENT`;

            const response = await DataService.AddPaymentTranaction(url);

            if (response.data) {
                vm.UniqueId = response.data[0].UniqueId;
                vm.TransactionId = response.data[0].TransactionId;

                return {
                    TransactionId: response.data[0].TransactionId,
                    UniqueId: response.data[0].UniqueId
                };
            }

            return null;
        }

    },
};
</script>

